@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
@media only screen and (min-width: 1024px) {
  .container header .header-nav-area #nav_container  {
    display:flex;
  }
}

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  background-color: #181d3d;
  background-image: url(/static/media/appBackground.e77a83fe.svg);
  background-repeat: no-repeat;
  background-size: 100%;
  background-attachment: fixed;
}

.webContainer{
  width: 100%;
  height: 100%;
}

.center{
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: auto;
  width: 100%;
}

.imageContainingDiv{
 height: 70%;
 width: 70%;
 margin: auto;
 overflow: hidden;
}

.imageSize{
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 48%;
  width: 48%;
}

.flexButtonContainer{
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  padding-bottom: 0;
  margin: 0;
  list-style: none;
}

.flexItem{
  padding-top: max(5px, 1%); 
  padding-bottom: max(5px, 1%);
  width: 200px;
  height: 100px; 
  margin-top: max(5px, 1%); 
  margin-bottom: max(5px, 1%);
  /* line-height: 5px; */
  color: white;
  font-weight: bold;
  font-size: 3em;
  text-align: center;
}

.submitButton{
  background-color: #f65364;   /*button fill colour*/
  padding: 3%;
  width: 100%;
  border-radius: 16px;
  color: white;
  border-color: #f65364;  
}

.submitButton:hover{
  background-color: #848699;
  color: white;
  border-color: #848699;  
}

.submitButton:active{
  background-color: #ffffff;   /*button fill colour*/
  color: rgb(0, 0, 0);
  border-color: #ffffff;  
}

.fixedTop{
  overflow: hidden;
  top: 0;
}

.fixedBottom{
  background-color: #181d3d;
  border-color: #181d3d;
  overflow: hidden;
  width:80%;
  bottom: 0;
  margin-left:auto;
  margin-right:auto;
}

/* button for all sreans except home screen */
