.textContent{
    color:white;
    width: 70%;
    margin-left: auto;
    margin-right:auto;
    padding-top: max(5px, 1%); 
    padding-bottom: max(5px, 1%);
}

.textContentLarge{
    color:white;
    width: 90%;
    margin-left: auto;
    margin-right:auto;
    padding-top: max(5px, 1%); 
    padding-bottom: max(5px, 1%);
    overflow: hidden;
    text-overflow: ellipsis; 
}

.imageSizeS{
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 20%;
    padding-top: max(5px, 1%); 
    padding-bottom: max(5px, 1%);
}
